<template>
<!--    <h1>RSF Tree here boi</h1>-->
    <div>
        <label>File
            <input type="file" id="file" ref="file" accept=".rsf" v-on:change="handleFileUpload()"/>
        </label>
        <div class="chart-container" id="chart">

        </div>
    </div>

</template>

<script>
    import * as d3 from 'd3';

    const margin = {top: 20, right: 90, bottom: 30, left: 90},
        width = 960 - margin.left - margin.right,
        height = 1000 - margin.top - margin.bottom;

    let treemap, svg, root;
    let i = 0, duration = 750;

    const treeData =
        {
            "name": "Top Level",
            "children": [
                {
                    "name": "Level 2: A",
                    "children": [
                        { "name": "Son of A" },
                        { "name": "Daughter of A" }
                    ]
                },
                { "name": "Level 2: B" }
            ]
        };

    export default {
        name: "RsfTree",
        data() {
            return {
                file: ''
            }
        },
        mounted() {
            // this.init(treeData);
            // this.update(root);
        },
        methods: {
            init(data) {
                treemap = d3.tree().size([height, width]);

                svg = d3.select("#chart").append("svg")
                    .attr("width", width + margin.right + margin.left)
                    .attr("height", height + margin.top + margin.bottom)
                    .append("g")
                    .attr("transform", "translate("
                        + margin.left + "," + margin.top + ")");

                // root = d3.hierarchy(flare);
                root = d3.hierarchy(data, function(d) { return d.children; });
                root.children.forEach((child, index, array) => {
                    if (child.children) {
                        child._children = child.children
                        child.children = null
                    }
                });
                root.x0 = height / 2;
                root.y0 = 0;
            },
            update(source) {
                let treeData = treemap(root);
                let nodes = treeData.descendants(),
                    links = treeData.descendants().slice(1);

                nodes.forEach(function(d){ d.y = d.depth * 180});

                // Update the nodes...
                let node = svg.selectAll('g.node')
                    .data(nodes, function(d) {return d.id || (d.id = ++i); });

                let nodeEnter = node.enter().append('g')
                    .attr('class', 'node')
                    .attr("transform", function(d) {
                        return "translate(" + source.y0 + "," + source.x0 + ")";
                    })
                    .on('click', this.click);

                nodeEnter.append('circle')
                    .attr('class', 'node')
                    .attr('r', 1e-6)
                    .style("fill", function(d) {
                        return d._children ? "lightsteelblue" : "#fff";
                    });

                nodeEnter.append('text')
                    .attr("dy", ".35em")
                    .attr("x", function(d) {
                        return d.children || d._children ? -13 : 13;
                    })
                    .attr("text-anchor", function(d) {
                        return d.children || d._children ? "end" : "start";
                    })
                    .text(function(d) { return d.data.name; });

                let nodeUpdate = nodeEnter.merge(node);

                nodeUpdate.transition()
                    .duration(duration)
                    .attr("transform", function(d) {
                        return "translate(" + d.y + "," + d.x + ")";
                    });

                nodeUpdate.select('circle.node')
                    .attr('r', 10)
                    .style("fill", function(d) {
                        return d._children ? "lightsteelblue" : "#fff";
                    })
                    .attr('cursor', 'pointer');

                let nodeExit = node.exit().transition()
                    .duration(duration)
                    .attr("transform", function(d) {
                        return "translate(" + source.y + "," + source.x + ")";
                    })
                    .remove();

                nodeExit.select('circle')
                    .attr('r', 1e-6);

                nodeExit.select('text')
                    .style('fill-opacity', 1e-6);

                let link = svg.selectAll('path.link')
                    .data(links, function(d) { return d.id; });

                let linkEnter = link.enter().insert('path', "g")
                    .attr("class", "link")
                    .attr('d', d => {
                        var o = {x: source.x0, y: source.y0}
                        return this.diagonal(o, o)
                    });

                let linkUpdate = linkEnter.merge(link);

                linkUpdate.transition()
                    .duration(duration)
                    .attr('d', d => { return this.diagonal(d, d.parent) });

                let linkExit = link.exit().transition()
                    .duration(duration)
                    .attr('d', d => {
                        var o = {x: source.x, y: source.y}
                        return this.diagonal(o, o)
                    })
                    .remove();

                nodes.forEach(function(d){
                    d.x0 = d.x;
                    d.y0 = d.y;
                });
            },
            diagonal(s, d) {

                return `M ${s.y} ${s.x}
                        C ${(s.y + d.y) / 2} ${s.x},
                          ${(s.y + d.y) / 2} ${d.x},
                          ${d.y} ${d.x}`
            },
            click(d) {
                if (d.children) {
                    d._children = d.children;
                    d.children = null;
                } else {
                    d.children = d._children;
                    d._children = null;
                }
                this.update(d);
            },
            handleFileUpload() {
                this.file = this.$refs.file.files[0];
                const reader = new FileReader();
                reader.onload = this.processRsf
                reader.readAsText(this.file);
            },
            processRsf(file) {
                let rsf = file.target.result;
                let rows = rsf.split('\n');
                let outTree = {
                    "name": "Root",
                    "children": []
                };
                let nodes = [];
                rows.forEach((row, index, array) => {
                        let entries = row.split(' ');
                        let parent = entries[1];
                        let child = entries[2];
                        array[index] = {
                            "parent": parent,
                            "child": child
                        }
                });
                rows.forEach((row, index, array) => {
                    if (row.parent == undefined) {
                        return;
                    }
                    let nodeParentIndex = nodes.findIndex( obj => obj.name == row.parent)
                    let childNode = {
                        "name": row.child,
                        "children": []
                    };
                    if (nodeParentIndex > -1) {
                        nodes[nodeParentIndex].children.push(childNode)
                    } else {
                        let parentNode = {
                            "name": row.parent,
                            "children": [childNode]
                        };
                        outTree.children.push(parentNode);
                        nodes.push(parentNode);
                        nodes.push(childNode);
                    }
                })

                // console.log(outTree);
                this.init(outTree);
                this.update(root);

            }
        }
    }
</script>

<style>
    .node circle {
        fill: #fff;
        stroke: steelblue;
        stroke-width: 3px;
    }

    .node text {
        font: 12px sans-serif;
    }

    .link {
        fill: none;
        stroke: #ccc;
        stroke-width: 2px;
    }
</style>
